import React from 'react'
import { Card, Table, Grid, Image } from 'semantic-ui-react'
import Moment from 'moment'

//Get List of token
const tokens = JSON.parse(localStorage.getItem('uniqueTokens'))
const contracts = JSON.parse(localStorage.getItem('uniqueAddress'))
const emptywallet = '0x0000000000000000000000000000000000000000';

var cumuGas = 0;
var cumuValue = {
  "BNB": 0
}

const BinanceLiquidityPoolsPane = ({ address }) => {
  // console.log('BinanceLiquidityPoolsPane-Transactions', address.binanceliquidityPools);
  // console.log('BinanceLiquidityPoolsPane-length', Object.keys(address.binanceBlocks).length);
  var i = 1;
  if (!address.binanceliquidityPools) return null
  return (
    <>
      <Card.Group itemsPerRow={1} stackable={true} doubling={true}>
        {Object.keys(address.binanceliquidityPools).map((poolname, poolindex) => (
          <Card key={poolname} className="fluid">
            <Card.Content>
              <Card.Header>
                <Grid columns='two' divided>
                  <Grid.Row>
                    <Grid.Column style={{ wordWrap: "break-word", fontWeight: "300", fontSize: "12px" }}>
                      <Grid.Row style={{ fontWeight: "1000", fontSize: "16px", marginBottom: "5px" }}>  {`${poolindex + 1}. Pool - ${poolname}`} </Grid.Row>
                    </Grid.Column>
                  </Grid.Row>
                </Grid>
              </Card.Header>
            </Card.Content>
            {address.binanceliquidityPools[poolname].map((data) => (
              <Card.Content extra>
                <Grid columns='two' divided>
                  <Grid.Row>
                    <Grid.Column width={4}>
                      <Grid.Row style={{ fontWeight: "600", fontSize: "16px", marginBottom: "10px", color: "#00b5ad" }}> {address.binanceBlocks[data.block].platform.tname.split('-')[1] + " Liquidity" || "Not Found"} </Grid.Row>
                      <Grid.Row style={{ fontWeight: "400", fontSize: "12px", marginBottom: "10px", color: "#000" }}> {`${Moment((address.binanceBlocks[data.block].transactions[0].timeStamp * 1000)).format('DD MMM YY hh:mm:ss a')}`} </Grid.Row>
                      <Grid.Row style={{ fontWeight: "400", fontSize: "14px", marginBottom: "10px" }}> <a href={"https://bsccan.com/tx/" + address.binanceBlocks[data.block].transactions[0].hash} target="_blank"> {`Block - ${data.block}`} </a> </Grid.Row>
                      {(data.reward != undefined)
                        ? <Grid.Row style={{ fontWeight: "500", fontSize: "14px", marginBottom: "10px", color: "#00b5ad" }}>
                          {`Earned $`}  {(address.tokensIndexed[data.reward.tokenAddress] != undefined)
                            ? (address.tokensIndexed[data.reward.tokenAddress].tokenInfo.price.rate * parseInt(data.reward.value) / Math.pow(10, address.uniqueTokens[data.reward.tokenAddress].decimal)).toFixed(4) : ''} {address.uniqueTokens[data.reward.tokenAddress].symbol}
                        </Grid.Row>
                        : <Grid.Row style={{ fontWeight: "400", fontSize: "16px", marginBottom: "10px", color: "#000" }}>  </Grid.Row>
                      }
                      <Grid.Row>
                        <Image src='../assets/gasfee.svg' /> &nbsp;
                        {`${cumuGasFee(parseFloat(address.binanceBlocks[data.block].gasFee))} BNB`}
                      </Grid.Row>
                    </Grid.Column>
                    <Grid.Column width={12}>
                      <Card.Content extra>
                        {address.binanceBlocks[data.block].transactions.map((tr, key) => (
                          (tr.contractAddress != "") ?
                            <Grid columns='three' divided>
                              <Grid.Row>
                                <Grid.Column>
                                  <Grid.Row style={{ wordWrap: "break-word", fontWeight: "400", fontSize: "12px", marginBottom: "10px" }}>
                                    {(tr.from == address.address) ? ' ' : (contracts[tr.from] != undefined) ? contracts[tr.from].name : (tokens[tr.from] != undefined) ? tokens[tr.from].name : "SomeWallet (" + tr.from + ")"}
                                    {(tr.to == address.address) ? ' ' : (contracts[tr.to] != undefined) ? contracts[tr.to].name : (tokens[tr.to] != undefined) ? tokens[tr.to].name : "SomeWallet (" + tr.to + ")"}                                </Grid.Row>
                                </Grid.Column>
                                <Grid.Column>
                                  {(tr.value == 0) ? <Image src='data:image/svg+xml;base64,' /> : (tr.to == address.address) ? <Image src='data:image/svg+xml;base64,' /> : <Image src='data:image/svg+xml;base64,' />}
                                  {(tr.value == 0) ? " Approve Transaction" : (tr.value / Math.pow(10, tr.tokenDecimal || 18)).toFixed(5) + ' '}
                                  {(tr.value == 0) ? " " : (tr.tokenSymbol || 'BNB ')}
                                  {(tr.value == 0) ? " " : (tr.to == address.address) ? ' In' : ' Out'}
                                </Grid.Column>
                                <Grid.Column>
                                  {(address.tokensIndexed[tr.contractAddress] != undefined) ? '$' + (address.tokensIndexed[tr.contractAddress].tokenInfo.price.rate * tr.value / Math.pow(10, tr.tokenDecimal || 18)).toFixed(4) : (tr.tokenSymbol == "WETH") ? '$' + (address.ETH.price.rate * tr.value / Math.pow(10, tr.tokenDecimal || 18)).toFixed(5) : ''}
                                </Grid.Column>
                              </Grid.Row>
                            </Grid>
                            : " "
                        ))}
                      </Card.Content>
                    </Grid.Column>
                  </Grid.Row>
                </Grid>
              </Card.Content>
            ))}
          </Card>
        ))}
      </Card.Group>
      <Card className="fluid">
        <Card.Content>
          <Card.Header>
            <Grid columns='three' divided>
              <Grid.Row>
                <Grid.Column>
                  {`Total`}
                </Grid.Column>
                <Grid.Column>
                  {`Assets`}
                </Grid.Column>
                <Grid.Column>
                  <Image src='../assets/gasfee.svg' /> &nbsp;
                  {`Gas Fees ${cumuGasFee(0)} BNB`}
                </Grid.Column>
              </Grid.Row>
            </Grid>
          </Card.Header>
        </Card.Content>
        <Card.Content extra>
          {Object.keys(cumuValue).map((token) => (
            <Grid columns='three' divided>
              <Grid.Row>
                <Grid.Column>

                </Grid.Column>
                <Grid.Column>
                  {`${token} : ${cumuValue[token]}`}
                </Grid.Column>
                <Grid.Column>

                </Grid.Column>
              </Grid.Row>
            </Grid>
          ))}
        </Card.Content>
      </Card>
    </>
  )
}

function cumuGasFee(gasFee, returnable = true) {
  // console.log(gasFee);
  cumuGas += gasFee;
  if (returnable)
    return cumuGas.toFixed(4);
  else
    return ' ';
}

function cumuTokenValue(tokenSymbol, tokenValue, credit = false) {
  if (cumuValue[tokenSymbol] == undefined) {
    cumuValue[tokenSymbol] = 0;
  }

  cumuValue[tokenSymbol] = credit ? cumuValue[tokenSymbol] + tokenValue : cumuValue[tokenSymbol] - tokenValue;
  return cumuValue[tokenSymbol].toFixed(4);
}

export default BinanceLiquidityPoolsPane