import React from 'react'
import { Card, Table, Grid, Image } from 'semantic-ui-react'
import Moment from 'moment'

//Get List of token
const tokens = JSON.parse(localStorage.getItem('uniqueTokens'))
const contracts = JSON.parse(localStorage.getItem('uniqueAddress'))
const emptywallet = '0x0000000000000000000000000000000000000000';

var cumuGas=0;
var cumuValue = {
  "Eth":0
}

var somewallet = [];
var somewalletIndexed = [];

const EtheriumBlocksPane = ({ address }) => {
  console.log('EtheriumliquidityPools', address.etheriumliquidityPools);
  console.log('EtheriumBlocksPane', address.etheriumBlocks);
  var i=1;

  cumuGas = 0;
  cumuValue = {
    "Eth": 0
  }

  function unknownWallet(addr) {
    if (somewalletIndexed[addr] == undefined) {
      somewalletIndexed[addr]="";
      somewallet.push({ address: addr });
    }
    
    return "SomeWallet (" + addr + ")";
  }

  function log(variable) {
    console.log(variable);
  }


  function cumuGasFee(gasFee, returnable = true) {
    // console.log(gasFee);
    cumuGas += gasFee;
    if (returnable)
      return cumuGas.toFixed(4);
    else
      return ' ';
  }

  function cumuTokenValue(tokenSymbol, tokenValue, credit = false) {
    if (cumuValue[tokenSymbol] == undefined) {
      cumuValue[tokenSymbol] = 0;
    }

    cumuValue[tokenSymbol] = credit ? cumuValue[tokenSymbol] + tokenValue : cumuValue[tokenSymbol] - tokenValue;
    return cumuValue[tokenSymbol].toFixed(4);
  }
  if (!address.etheriumBlocks) return null
  return (
    <>
      <Card.Group itemsPerRow={1} stackable={true} doubling={true}>
        {Object.keys(address.etheriumBlocks).map((blockKey, blockindex) => (
          <Card key={blockKey} className="fluid">
            <Card.Content>
              <Card.Header>
                <Grid columns='four' divided>
                  <Grid.Row>
                    <Grid.Column style={{ wordWrap: "break-word", fontWeight: "300", fontSize: "12px"}}>
                      <Grid.Row style={{ fontWeight: "1000", fontSize: "16px", marginBottom: "5px" }}> <a href={"https://etherscan.io/tx/" + address.etheriumBlocks[blockKey].transactions[0].hash} target="_blank"> {`${blockindex + 1}. Block - ${blockKey}`} </a> </Grid.Row>
                      <Grid.Row> {`${Moment((address.etheriumBlocks[blockKey].transactions[0].timeStamp * 1000)).format('YYYY-MM-DD')}`} </Grid.Row>
                      <Grid.Row> {`${Moment((address.etheriumBlocks[blockKey].transactions[0].timeStamp * 1000)).format('hh:mm:ss a')}`} </Grid.Row>
                      <Grid.Row> {`${Moment((address.etheriumBlocks[blockKey].transactions[0].timeStamp * 1000)).fromNow()}`} </Grid.Row>
                    </Grid.Column>
                    <Grid.Column style={{ wordWrap: "break-word", fontWeight: "300", fontSize: "10px"}}>
                      <Grid.Row style={{ fontWeight: "1000", fontSize: "16px", marginBottom: "5px" }}> {address.etheriumBlocks[blockKey].platform.tname || "Not Found"} </Grid.Row>
                      <Grid.Row> {address.etheriumBlocks[blockKey].platform.name || "My Wallet"} </Grid.Row>
                      <Grid.Row> {address.etheriumBlocks[blockKey].platform.address || address.address} </Grid.Row>
                      <Grid.Row> {`${Object.keys(address.etheriumBlocks[blockKey].in).length} In, ${Object.keys(address.etheriumBlocks[blockKey].out).length} Out, ${Object.keys(address.etheriumBlocks[blockKey].approve).length} Approve`} </Grid.Row>
                    </Grid.Column>
                    <Grid.Column style={{ wordWrap: "break-word", fontWeight: "300", fontSize: "14px" }}>
                      <Grid.Row style={{ fontWeight: "1000", fontSize: "16px", marginBottom: "5px" }}> 
                        {(address.etheriumBlocks[blockKey].blockLabel != undefined)
                          ? <button> {`Track ${(address.etheriumBlocks[blockKey].blockLabel)}`} </button>
                        :""}
                      </Grid.Row>
                      {(address.etheriumBlocks[blockKey].in.length > 0 && address.etheriumBlocks[blockKey].out.length > 0)
                        ? <Grid.Row style={{ fontWeight: "600", fontSize: "16px" }}>
                          <Grid columns='two' divided>
                            <Grid.Row>
                              <Grid.Column>
                                {address.etheriumBlocks[blockKey].out.map((trr, keyr) => (
                                  <Grid.Row>
                                    {(trr.tokenValue > 0) ? (trr.tokenValue / Math.pow(10, trr.tokenDecimal || 18)).toFixed(2) + ' ' + (trr.tokenSymbol || 'Ether ') : ''}
                                  </Grid.Row>
                                ))}
                              </Grid.Column>
                              <Grid.Column>
                                {address.etheriumBlocks[blockKey].in.map((trr, keyr) => (
                                  <Grid.Row>
                                    {(trr.tokenValue > 0) ? (trr.tokenValue / Math.pow(10, trr.tokenDecimal || 18)).toFixed(2) + ' ' + (trr.tokenSymbol || 'Ether ') : ''}
                                  </Grid.Row>
                                ))}
                              </Grid.Column>
                            </Grid.Row>
                          </Grid>
                        </Grid.Row>
                        : ''}
                    </Grid.Column>
                    <Grid.Column>
                      <Grid.Row style={{ fontWeight: "300", fontSize: "14px" }}>
                        <Image src='../assets/gasfee.svg' /> &nbsp; &nbsp; &nbsp;
                        {`:  ${address.etheriumBlocks[blockKey].gasFee} `}
                      </Grid.Row>
                      <Grid.Row style={{ fontWeight: "400", fontSize: "14px" }}>
                        {`Total : ${cumuGasFee(parseFloat(address.etheriumBlocks[blockKey].gasFee))} Eth`}
                      </Grid.Row>
                    </Grid.Column>
                  </Grid.Row>
                </Grid>
              </Card.Header>
            </Card.Content>
            <Card.Content extra>
              {address.etheriumBlocks[blockKey].transactions.map((tr, key) => (
                <Grid columns='four' divided>
                  <Grid.Row>
                    <Grid.Column>
                      <Grid.Row>
                        <strong>
                          {(tr.from == address.address) ? ' ' : (contracts[tr.from] != undefined) ? contracts[tr.from].name : (tokens[tr.from] != undefined) ? tokens[tr.from].name : unknownWallet(tr.from)}
                          {(tr.to == address.address) ? ' ' : (contracts[tr.to] != undefined) ? contracts[tr.to].name : (tokens[tr.to] != undefined) ? tokens[tr.to].name : unknownWallet(tr.to)}
                        </strong>
                      </Grid.Row>
                      {/* trade exchange */}
                      {/* <Image src='data:image/svg+xml;base64,' /> */}
                      {/* contract */}
                      {/* <Image src='data:image/svg+xml;base64,' /> */}
                      {/* outgoing */}
                      {/* <Image src='data:image/svg+xml;base64,' /> */}
                      {/* incoming */}
                      {/* <Image src='data:image/svg+xml;base64,' /> */}
                    </Grid.Column>
                    <Grid.Column>
                      {(tr.value==0) ? <Image src='data:image/svg+xml;base64,' /> : (tr.to == address.address) ? <Image src='data:image/svg+xml;base64,'/> : <Image src='data:image/svg+xml;base64,'/> }
                      {(tr.value == 0) ? " Approve Transaction" : (tr.value / Math.pow(10, tr.tokenDecimal || 18)).toFixed(4)+' '}
                      {(tr.value == 0) ? " " : (tr.tokenSymbol || 'Ether ') }
                      {(tr.value == 0) ? " " : (tr.to == address.address) ? ' In' : ' Out'}
                    </Grid.Column>
                    <Grid.Column>
                      <Grid.Row>
                        {(tr.value == 0) ? " " :'Balance: ' }
                        {(tr.value == 0) ? " " : cumuTokenValue(tr.tokenSymbol || 'Eth', (tr.value / Math.pow(10, 18)), (tr.to === address.address))}
                        {(tr.value == 0) ? " " : (tr.tokenSymbol || ' Eth')}
                      </Grid.Row>
                    </Grid.Column>
                    <Grid.Column>
                      {tr.type}
                    </Grid.Column>
                  </Grid.Row>
                </Grid>
              ))}
            </Card.Content>
          </Card>
        ))}
      </Card.Group>
      <Card className="fluid">
        <Card.Content>
          <Card.Header>
            <Grid columns='three' divided>
              <Grid.Row>
                <Grid.Column>
                  {`Total`}
                </Grid.Column>
                <Grid.Column>
                  {`Assets`}
                </Grid.Column>
                <Grid.Column>
                  <Image src='../assets/gasfee.svg' /> &nbsp;
                  {`Gas Fees ${cumuGasFee(0)} Eth`}
                </Grid.Column>
              </Grid.Row>
            </Grid>
          </Card.Header>
        </Card.Content>
        <Card.Content extra>
          {Object.keys(cumuValue).map((token) => (
            <Grid columns='three' divided>
              <Grid.Row>
                <Grid.Column>
                  
                </Grid.Column>
                <Grid.Column>
                  {`${token} : ${cumuValue[token]}`}
                </Grid.Column>
                <Grid.Column>
                  
                </Grid.Column>
              </Grid.Row>
            </Grid>
          ))}
          {log(somewallet)}
        </Card.Content>
      </Card>
    </>
  )
}

export default EtheriumBlocksPane